* {
  margin: 0px;
}
h1,
p {
  font-family: Lato;
}
html,
body {
  height: 100%;
  padding: 0%;
}

.ima {
  height: 200px;
  width: 350px;
  margin-top: auto;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  left: 0;
  right: 0;
  z-index: 0;
}
#body {
  opacity: 0.8;
  width: 100%;
  min-height: 100vh;
  top: 0px;
  left: 0px;
  z-index: 1000;
  position: relative;
  overflow: hidden;
  padding: 0%;
  margin: 0px;
}
div.ima {
  border: 3px solid #4903fc;
  border-radius: 10px;
}
.subtitle {
  position: relative;
  margin: auto;
  text-align: center;
  color: #7d27ab;
  text-shadow: #030002;
  font-weight: bold;
  z-index: 1;
}
.backgroundimg {
  opacity: 0.5;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: auto;
}
a {
  font-size: 30px;
  text-decoration: none;
  position: relative;
}
a:visited {
  color: #00a56e;
}
footer {
  position: relative;
  display: flex;
  bottom: 0px;
  height: 100vh;
  width: 100%;
  background: #0345fc;
  align-items: center;
  justify-content: center;
}
footer div {
  height: 10vp;
  width: 10vp;
  background-color: #009688;
  border: 1px;
  border-style: double;
  position: absolute;
  top: 20vh;
}
button {
  padding: 10px;
  background-color: #9c27b0;
  border-radius: 8px;
  box-shadow: 1px 1px 2px 2px #2c2b2b;
}
